.card-default-img {
  max-width: 192px;
  max-height: 192px;
  height: auto;
  width: auto;
  display: block;
  margin: auto;
  margin-top: 0.5em;
  margin-bottom: 1em;
  border-radius: 1em;
}

.card-default-footer {
  background: var(--light-grey-color);
  border-radius: 0 0 1em 1em;
  padding: 1em 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.card-alternate-footer {
  background: var(--light-grey-color);
  border-radius: 0 0 1em 1em;
  padding: 1em 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.card-default-body {
  padding: 1rem;
  overflow-y: auto;
  width: 100%;
  color: #333;
}

.card-alternate-variant {
  background: var(--background-card-light);
  border-style: none;
  border-radius: 1em;
  display: flex;
  flex-direction: column;
  min-width: 300px;
  min-height: 4rem;
  border: 2px solid var(--secondary-color);
  box-shadow: var(--default-shadow);
}

.card-default-variant {
  background-color: #ffffff;
  border-radius: 1em;
  border: 1px solid var(--grey-border);
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  min-width: 300px;
  min-height: 4rem;
  height: fit-content;
  box-shadow: var(--default-shadow);
}

.card-default-footer {
  width: 100%;
}

.card-header {
  font-size: 1.5rem;
  font-weight: bold;
  color: #222;
}

.card-subtitle {
  margin-top: 0.4em;
  font-size: 1.1rem;
  color: #555;
  font-style: italic;
  font-weight: 300;
}

.card-text {
  color: #555;
  margin-top: 1em;
  line-height: 1.65;
  font-size: 1.1rem;
}

@media (max-width: 768px) {
  .card-header {
    font-size: 1.25rem;
    margin-bottom: 0.4em;
  }

  .card-subtitle {
    font-size: 1rem;
    margin-top: 0.4em;
  }

  .card-text {
    font-size: 1rem;
    margin-top: 0.75em;
    line-height: 1.5;
  }
}
